@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  padding: 0;

  font-family: "Quicksand";
  color: #333;
}
.App{
  height: 100%;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;

}
.navbar .links {
  margin-left: auto;
  font-weight: bold;
  font-size: 18px;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}
.content {
  max-width: 600px;
  height:100%;
  margin: 40px auto;
  padding: 20px;
  background-color:rgb(194 194 185);
}
.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}
.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}
.blog-preview a{
  text-decoration: none;
}
.blog-details h2{
      margin-bottom: 16px;
      color: red;
}
.blog-details p{
    margin-bottom: 9px;
    font-style: italic;
    font-weight: 1000;
    text-transform: uppercase;
}
.blog-details div{
  margin: 20px 0;
}
.blog-details button{
  width: 5em;
  height: 2em;
  border-radius: 10px;
}
/* add a new blog */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
  font-weight: bold;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create #author{
  height: 30px;
}
.create #Body{
  height: 200px;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.create button:hover{
  color: #a14a64;
}
/* notfound page */

.notFound h2{
  color: red;
  padding-left: 40%;
  margin-bottom: 30px;
  font-size: 40px;
}
.notFound p{
  padding-left: 35%;
  font-size: 30px;
  font-weight: bold;
  
}


